
#buyer_category_list { min-height: calc(100vh - 136px); margin-top: 83px; }
/* #buyer_category_list .home_video { min-height: 500px; height:calc(100vh - 136px) !important; } */
#buyer_category_list .searchbox { width: 100%; padding: 5px 30px 15px; margin-top: 30px; }
#buyer_category_list .searchbox form { width: 100%; position: relative; padding: 0px 0px 0px 0px; }
#buyer_category_list .searchbox form .inputbox { width: 100%; position: relative;}
#buyer_category_list .searchbox form .inputbox input { font-size: 15px; display: inline-block; border-radius: 10px !important; width: 100%; float:left; height: 45px; border: 1px solid #ddd !important; padding: 10px 10px; line-height: 25px; color:#333; }
#buyer_category_list .searchbox form .inputbox button { position: absolute; top: 0px; right: 0px; border:0px; display: inline-block; width: 45px; height: 45px; text-align: center; line-height: 45px; background:rgb(202, 52, 100); color:#fff; border-top-right-radius: 10px; border-bottom-right-radius: 10px; }
#buyer_category_list .searchbox form .inputbox button:hover { background:#000;  }
#buyer_category_list .mask.d-flex { width: 100%; }

#buyer_category_list .searchbox form .inputbox input::placeholder { color: #333 !important; font-size: 12px !important;}
#buyer_category_list .searchbox form .inputbox input::-webkit-input-placeholder { color: #ddd;}
#buyer_category_list .searchbox form .inputbox input::-moz-placeholder { color: #ddd;}
#buyer_category_list .searchbox form .inputbox input:-ms-input-placeholder { color: #ddd;}
#buyer_category_list .searchbox form .inputbox input:-moz-placeholder { color: #ddd;}

#buyer_category_list .searchbox form .selectbox { display: inline-block; position: relative; margin-left: 0px; width: 150px; position: absolute; top: 0px; right: 0px; }
#buyer_category_list .searchbox form .selectbox i { position: absolute; top: 11px; left: 20px; font-size: 22px; z-index: 3; }
#buyer_category_list .searchbox form .selectbox .cat_links { position: relative; }
#buyer_category_list .searchbox form .selectbox .cat_links > a { display: block; position:relative; z-index: 2; width: 100%; border-radius: 10px; height: 45px; border: 1px solid #ddd; padding: 10px 20px 5px 55px; line-height: 20px; color:#333; }
#buyer_category_list .cat_links a { color:#333; }
#buyer_category_list .searchbox form .selectbox #cat_links { position: absolute; top: 50px; right: 0px; width: 300px; z-index: 99; height: auto; border: 1px solid #ddd; padding: 10px 10px; background:#fff; }
#buyer_category_list .searchbox form .selectbox #cat_links h4 { font-size: 18px; padding-left: 10px; }
#buyer_category_list .selectbox #cat_links ul { display: block; overflow: hidden; padding: 0px; margin: 0px; }
#buyer_category_list .selectbox #cat_links ul li { width: 50%; float:left; display: block; margin: 0px; }
#buyer_category_list .selectbox #cat_links ul li a { display: block; text-transform: capitalize; font-size: 15px; line-height: 20px; padding: 10px 10px;}
#buyer_category_list .selectbox #cat_links ul li a:hover { background:#e5e5e5;  }
#buyer_category_list .selectbox #cat_links { display: none; box-shadow: 0px 0px 5px -3px #000; transition: none; }
#buyer_category_list .selectbox #cat_links.show, #buyer_category_list .selectbox #cat_links.collapsing { display: block !important; transition: none; }
